// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
	 vm.$u.api = {};
	
	// 
	vm.$u.api.index = (params = {}) => vm.$u.get('api/index', params);
	
	// 
	vm.$u.api.authLogin = (params = {}) => vm.$u.post('api/auth/login', params);// 
	vm.$u.api.authRegister = (params = {}) => vm.$u.post('/api/auth/register', params);// 
	vm.$u.api.authLogout = () => vm.$u.post('/api/auth/logout');// 
	vm.$u.api.authOssToken = () => vm.$u.get('/api/auth/oss/token');// OSS Token
	
	// 
	vm.$u.api.userInfo = () => vm.$u.get('api/user'); // /api/user
	vm.$u.api.userInfoUpdate = (params = {}) => vm.$u.put('/api/user', params);// 
	vm.$u.api.userUpdateAvatar = (params = {}) => vm.$u.patch('/api/user/avatar', params);// 
	
	// 
	vm.$u.api.goodsDetail = goodsId => vm.$u.get(`/api/goods/${goodsId}`);// 
	vm.$u.api.goodsList = (params = {}) => vm.$u.get(`/api/goods`,params);// 
	
	// 
	vm.$u.api.goodsCollect = id => vm.$u.post(`/api/collects/goods/${id}`);// 
	vm.$u.api.getCollected = () => vm.$u.get(`/api/collects`);  //

//
vm.$u.api.cartAdd = params => vm.$u.post(`/api/carts`,params); //
vm.$u.api.cartList = goods => vm.$u.get(`/api/carts?include=goods`);  //

	vm.$u.api.cartNumber = (cart,num) => vm.$u.put(`/api/carts/${cart}`,{num});  //
	
	vm.$u.api.cartDelete = cart => vm.$u.delete(`/api/carts/${cart}`);  //
	vm.$u.api.cartSelect = (params = {}) => vm.$u.patch(`/api/carts/checked`,params);  //
	
	//
	vm.$u.api.getAdress = () => vm.$u.get(`/api/address`);  //
	
	vm.$u.api.EditDefault = (address) => vm.$u.patch(`/api/address/${address}/default`,address); //
	vm.$u.api.addA = (address) => vm.$u.post(`/api/address`,address); //
	
	vm.$u.api.deleteA = (address) => vm.$u.delete(`/api/address/${address}`,address); //
	
	//
	vm.$u.api.getOrder = () => vm.$u.get(`/api/orders/preview`);  //
	
	
	
}

export default {
	install
}